<template>
    <a-card>
        <a-alert type="info">
            <div slot="description">
                百度地图拾取坐标系统{{height}}
                <a href="http://api.map.baidu.com/lbsapi/getpoint/index.html" target="_blank">http://api.map.baidu.com/lbsapi/getpoint/index.html</a>
            </div>
        </a-alert>
        <div style="height: 10px"></div>
        <a-spin :spinning="spinning" tip="地图加载中">
            <div id="allmap" style="height:400px;width: 100%">

            </div>
        </a-spin>
        <CheckModal :visible="visible" :data="row"/>
    </a-card>
</template>

<script>
    import {getDepartmentCoordinateList} from '@/api/department'
    import CheckModal from './components/Check'

    export default {
        name: "Index",
        components: {CheckModal},
        data() {
            return {
                spinning: false,
                map: {},
                visible: false,
                row: {},
            }
        },
        methods: {
            init() {
                this.spinning = true;
                let departmentId = this.$store.state.common.department_id;
                getDepartmentCoordinateList(departmentId).then(res => {
                    this.spinning = false;
                    let data = res.data;
                    if (!data.length) {
                        this.$message.error('获取支部坐标失败');
                        return false;
                    }

                    let x = data[0]['coordinates_format'][0];
                    let y = data[0]['coordinates_format'][1];
                    this.mapInit(x, y);

                    for (let i = 0; i < data.length; i++) {
                        this.mapSetPoint(data[i]);
                    }
                });
            },
            mapInit(x, y) {
                this.map = new BMap.Map("allmap");    // 创建Map实例
                this.map.centerAndZoom(new BMap.Point(x, y), 15);  // 初始化地图,设置中心点坐标和地图级别
                //添加地图类型控件
                this.map.addControl(new BMap.MapTypeControl({
                    mapTypes: [
                        BMAP_NORMAL_MAP,
                        BMAP_HYBRID_MAP
                    ]
                }));
                this.map.setCurrentCity("呼和浩特市");          // 设置地图显示的城市 此项是必须设置的
                this.map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放
            },
            mapSetPoint(row) {
                let x = row['coordinates_format'][0];
                let y = row['coordinates_format'][1];

                let myIcon = new BMap.Icon(require('@/assets/pc/images/dhico.png'), new BMap.Size(50, 50));
                let pt = new BMap.Point(x, y);
                let marker = new BMap.Marker(pt, {icon: myIcon});  // 创建标注
                this.map.addOverlay(marker);              // 将标注添加到地图中

                marker.addEventListener("click", () => {
                    this.row = row;
                    this.visible = false;
                    this.visible = true;
                });
            }
        },
        mounted() {
            this.init();

            $("#allmap").height(document.documentElement.clientHeight-330);
            window.onresize = ()=>{
                $("#allmap").height(document.documentElement.clientHeight-330);
            };
        }
    }
</script>

